<template>
  <div id="basicINfo">
    <div class="main">
      <div class="tt">个人资料</div>

      <el-form :model="form" :rules="rules" style="margin-top:60px;width:60%" ref="form"> 
         <el-form-item label="出生地："  :label-width="formLabelWidth" prop="BirthPlace" placement="right">
              <el-input v-model="form.BirthPlace" clearable></el-input>
            </el-form-item>
          <el-form-item label="籍贯：" :label-width="formLabelWidth" prop="NativePlace" placement="right">
              <el-input v-model="form.NativePlace" clearable></el-input>
            </el-form-item>
            <el-form-item label="证件号码：" :label-width="formLabelWidth" prop="IDNo">
              <el-input v-model="form.IDNo" clearable></el-input>
            </el-form-item>
            <el-form-item label="证件有效期：" :label-width="formLabelWidth" prop="IDValid" placement="right">
              <el-date-picker v-model="form.IDValid" type="date" clearable></el-date-picker>
            </el-form-item>
            <el-form-item label="信仰宗教：" :label-width="formLabelWidth" prop="Religion" :loading="true">
              <el-select filterable v-model="form.Religion" placeholder="请选择">
                <el-option
                  v-for="(item,index) in Religionoptions"
                  :key="index"
                  :label="item.label"
                  :value="item.value"
                ></el-option>
              </el-select>
            </el-form-item> 
            <el-form-item label="港澳台侨外：" :label-width="formLabelWidth" prop="GATQW" :loading="true">
              <el-select filterable v-model="form.GATQW" placeholder="请选择">
                <el-option
                  v-for="(item,index) in GATQWoptions"
                  :key="index"
                  :label="item.label"
                  :value="item.value"
                ></el-option>
              </el-select>
            </el-form-item> 
            <el-form-item label="政治面貌：" :label-width="formLabelWidth" prop="Political" :loading="true">
              <el-select filterable v-model="form.Political" placeholder="请选择">
                <el-option
                  v-for="(item,index) in Politicaloptions"
                  :key="index"
                  :label="item.label"
                  :value="item.value"
                ></el-option>
              </el-select>
            </el-form-item> 
            <el-form-item label="是否独生子女：" :label-width="formLabelWidth" prop="OneChild" :loading="true">
              <el-select filterable v-model="form.OneChild" placeholder="请选择">
                <el-option
                  v-for="(item,index) in OneChildoptions"
                  :key="index"
                  :label="item.label"
                  :value="item.value"
                ></el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="血型码：" :label-width="formLabelWidth" prop="Blood" :loading="true">
              <el-select filterable v-model="form.Blood" placeholder="请选择">
                <el-option
                  v-for="(item,index) in Bloodoptions"
                  :key="index"
                  :label="item.label"
                  :value="item.value"
                ></el-option>
              </el-select>
            </el-form-item>
             <el-form-item label="工作年月：" :label-width="formLabelWidth" prop="WorkingDate" placement="right">
              <el-date-picker v-model="form.WorkingDate" type="month" clearable></el-date-picker>
            </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button :loading="createLoading1" type="primary" @click="comfirmFormHandler('form')">
          <i class="el-icon-check el-icon--left"></i>保 存
        </el-button>
        <el-button @click="resetForm">
          <i class="el-icon-close el-icon--left"></i>重 置
        </el-button>
      </div> 
    </div>
  </div>
</template>

<script>
import http from "../../../../utils/http";
import api from "../../../../utils/api";
export default {
  name: "",
  components: {},
  props: {},
  data() {
    return { 
      createLoading1:false,
      formLabelWidth: "160px", 
      Religionoptions:[],
      Bloodoptions:[],
      GATQWoptions:[],
      Politicaloptions:[],
      TeacherID:"",
      userid:"",
      username:"",
      OneChildoptions: [
        {
          value: "0",
          label: "不是独生子女"
        },
        {
          value: "1",
          label: "是独生子女"
        }
      ],
      form: {
        BirthPlace: "",
        NativePlace: "",
        IDNo: "",
        IDValid: "",
        Religion: "",
        GATQW: "",
        Political: "",
        OneChild: "",
        Blood: "",
        WorkingDate: ""
      },
      rules: {}
    };
  },
  computed: {},
  watch: {}, 
  mounted() {
  let userInfo = http.getUser();
    if (userInfo) {
        this.userid = userInfo.UserID;
        this.username = userInfo.UserName;
      }
    this.Initdata();
    this.getdata();
 },
  methods: {
    async Initdata() {  
      this.Politicaloptions = []; 
      this.Bloodoptions = []; 
      this.Religionoptions = [];
      this.GATQWoptions = [];  
      this.getMetadatas("GB/T 4762", this.Politicaloptions); 
      this.getMetadatas("JY/T 1001 XX", this.Bloodoptions);
      this.getMetadatas("GA 214.12", this.Religionoptions);
      this.getMetadatas("JY/T 1001 GATQW", this.GATQWoptions);
      const res = await http.get(api.getNatAllData, {
        Token: http.getToken()
      });
      if (!res.type) {
        let schoolops = [];
        res.Datas.forEach(element => {
          schoolops.push({
            value: element.NationalityCode,
            label: element.NationalityShortName
          });
          this.Natoptions = schoolops;
        });
      }
    },
    async getdata() {
      const res = await http.get(api.getBasicInfo, {
          uid: this.userid,
          Token: http.getToken()
        });
        if (!res.Result) {
          this.$message({
            showClose: true,
            message: res.message,
            type: res.type
          });
        } else {
          if(res.Data != null && res.Data.length > 0) { 
             this.TeacherID = res.Data[0].TeacherID; 
             this.form.BirthPlace = res.Data[0].TeacherBirthPlace;
             this.form.NativePlace = res.Data[0].TeacherNativePlace;
             this.form.IDNo = res.Data[0].TeacherIDNO;
             this.form.IDValid = res.Data[0].TeacherIDValid;
             this.form.Religion = res.Data[0].TeacherReligion;
             this.form.GATQW = res.Data[0].TeacherGATQWM;
             this.form.Political = res.Data[0].TeacherPolitical;
             this.form.OneChild = res.Data[0].TeacherOneChild;
             this.form.Blood = res.Data[0].TeacherBlood;
             this.form.WorkingDate = res.Data[0].TeacherWorkingDate;
          }
        }
    },
    // 表单确认,添加用户
    comfirmFormHandler(formName) {
      this.$refs[formName].validate(valid => { 
        if (valid) { 
          this.savesubmit();
        } else {
          this.$message({
            showClose: true,
            message: "请输入正确格式",
            type: "error"
          });
          return false;
        }
      });
    },
    savesubmit: async function() {
      this.createLoading1 = true; 
       var idValid = "";
      if (this.form.IDValid) {
        var i = new Date(this.form.IDValid);
        idValid =
          i.getFullYear() + "-" + (i.getMonth() + 1) + "-" + i.getDate();
      }
      var wkDate = "";
      if (this.form.WorkingDate != "-" && this.form.WorkingDate != "") {
        console.dir(this.form.WorkingDate);
        var w = new Date(this.form.WorkingDate);
        wkDate = w.getFullYear() + ("0" + (w.getMonth() + 1)).slice(-2);
      }
      this.saveData({
        id: this.TeacherID,
        userid: this.userid,
        BirthPlace:this.form.BirthPlace,
        NativePlace:this.form.NativePlace,
        IDNo:this.form.IDNo,
        IDValid:idValid,
        Religion:this.form.Religion,
        GATQW:this.form.GATQW,
        Political:this.form.Political,
        OneChild:this.form.OneChild,
        Blood:this.form.Blood,
        WorkingDate:wkDate,
        Token: http.getToken()
      });
    },
    async saveData(params) {
       const res = await http.post(api.updatePersonThc, params);
      if (!res.Result) {
        this.$message({
          showClose: true,
          message: res.message,
          type: res.type
        });
      } else {
        this.$message({
          showClose: true,
          message: "数据保存成功！",
          type: "success"
        });
      } 
      this.createLoading1 = false;
    },
    resetForm() {
      this.$refs["form"].resetFields();
      this.$refs.form.clearValidate();
    },
    async getMetadatas(code, opts) {
      const res = await http.get(api.getMetadataTrees, {
        code: code,
        token: http.getToken()
      });
      if (!res.Result) {
        this.$message({
          showClose: true,
          message: res.message,
          type: res.type
        });
      } else {
        res.Datas.forEach(element => {
          if (element.MetadataCode != code) {
            opts.push({
              value: element.MetadataCode,
              label: element.MetadataName
            });
          }
        });
      }
    },
  }
};
</script>

<style lang='less'>
#basicINfo {
  text-align: left;
  width: 956px;
  height: 1017px;
  background: rgba(255, 255, 255, 1);
  border-radius: 3px;
  //
  padding: 20px;
  padding-top: 0;
  .main {
    .tt {
      color: #333;
      font-size: 14px;
      border-bottom: 1px solid #ecf1f7;
      line-height: 50px;
    } 
    .dialog-footer {
        width: 100%;
        display: block;
        text-align: center;
        margin-top: 36px;

    }
    .ct {
      color: #666;
      font-size: 12px;
      padding: 50px 170px 50px 50px;
    //   border: 1px solid #000;
      .item {
        margin-bottom: 30px;
        display: flex;
        justify-content: space-around;
        //   border: 1px solid #000;
        .pt {
          padding-top: 10px;
        }
        .left,
        .right {
          display: flex;
          justify-content: space-around;
          //   border: 1px solid #000;
          width: 50%;
          .text {
            width: 30%;
            line-height: 40px;
            text-align: right;
            //   border: 1px solid red;
          }
          .input {
            width: 65%;
            .el-select {
              width: 100%;
            }
            img{
                // margin-left: 40px;
                margin-right: 20px;
            }
          }
          .df{
              display: flex;
              .llwj {
            // vertical-align: top;
            button {
              width: 110px;
              height: 30px;
              background: rgba(237, 237, 237, 1);
              border: 1px solid rgba(221, 221, 221, 1);
              border-radius: 3px;
              color: #666666;
            }
          }   
          }
       
        }
      }
    }
  }
}
</style>


